<template>
    <div class="content">
        <div class="container">
            <div class="col-lg-4 col-md-6 ml-auto mr-auto movil">
                <form class="form" id="login-form" @submit.prevent="login">
                    <div class="card card-login card-white">
                        <div class="card-header">
                            <img src="/img/card-primary.png" alt="">
                            <h1 class="card-title">Log in</h1>
                        </div>
                        <div class="card-body">
                            <div class="form-group mb-0 " :class="[errorClass['email']]">
                                <div class="input-group">
                                    <div class="input-group-prepend">
                                        <div class="input-group-text">
                                            <i class="tim-icons icon-email-85"></i>
                                        </div>
                                    </div>
                                    <input type="email" class="form-control" id="email" name="email" placeholder="Correo" v-model="email" @change="validateEmail">
                                </div>
                              <Feedback v-if="error['email']" :message="error['email']" />
                              <template v-if="error['errors']">
                                <div  v-for="(item) in error['errors'].email" :key="item">
                                  <Feedback v-if="item" :message="item"   />
                                </div>
                              </template>
                            </div>
                            <div class="form-group mb-0 " :class="[errorClass['password']]">
                                <div class="input-group">
                                    <div class="input-group-prepend">
                                        <div class="input-group-text">
                                            <i class="tim-icons icon-lock-circle"></i>
                                        </div>
                                    </div>
                                    <input type="password" class="form-control" id="password" name="password" placeholder="contraseña" v-model="password" @change="validatePassword">
                                </div>
                              <Feedback v-if="error['password']" :message="error['password']" />
                              <template v-if="error['errors']">
                                <div v-for="(item) in error['errors'].password" :key="item">
                                  <Feedback v-if="item" :message="item"   />
                                </div>
                              </template>
                            </div>
                        </div>
                        <div class="card-footer">
                            <button class="btn btn-primary btn-lg btn-block mb-3" type="submit" >Iniciar sesión</button>
                            <div class="pull-left">
                                <h6>
                                    <a href="password.request" class="text-light">
                                        <small>¿Olvidaste tu contraseña?</small>
                                    </a>
                                </h6>
                            </div>
                            <div class="pull-right">
                                <h6>
                                    <a href="register" class="text-light">
                                        <small>Crear una nueva cuenta</small>
                                    </a>
                                </h6>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import Feedback from "@/components/Alerts/Feedback";
import httpClient from "@/plugins/AxiosGlobal";

export default {
    name: "Login",
    components: {Feedback},
    data() {
        return {
          email: null,
          password: null,
          errorClass: [],
          error: [],
        }
    },
    created() {
        const element = document.getElementById('TempleGuest');
        element.className = 'full-page login-page';
    },
    methods: {
        validateEmail() {
          if (!this.email) {
            this.errorClass['email'] = 'has-danger';
            this.error['email']  = null;
          }
          if (this.email.includes('@')) {
            this.errorClass['email'] = 'has-success';
            this.error['email']  = null;
          }
          else {
            this.errorClass['email'] = 'has-danger';
            this.error['email']  = null;
          }
        },
        validatePassword() {
          if (!this.password) {
            this.errorClass['password'] = 'has-danger';
            this.error['password']  = null;
          }else {
            this.errorClass['password'] = 'has-success';
            this.error['password']  = null;
          }
        },
        async login() {
          if (!this.email) {
            this.error['email']  = 'El correo electrónico no puede estar vacío';
            return this.errorClass['email'] = 'has-danger';
          }
          if (!this.password) {
            this.error['password']  = 'La contraseña no puede estar vacía';
            return this.errorClass['password'] = 'has-danger';
          }
          await httpClient.post('/login',  {email: this.email, password: this.password, device_name: navigator.platform})
                     .then(response => {
                       this.error['password']  = null;
                       this.error['email']  = null;
                       sessionStorage.setItem('token', response.data);
                       this.$store.dispatch('Auth/getUser');
                       window.location.assign("/home")
                     })
                     .catch(err => {
                       this.errorClass['email'] = 'has-danger';
                       this.errorClass['password'] = 'has-danger';
                       this.error['errors']  = err.response.data.errors;
                     })
        },
    }
}
</script>

<style scoped>
    @media (max-width: 850px){
        .full-page > .content {
            padding-bottom: 0;
            padding-top: 21px;
        }
    }
</style>
